@import './fonts.scss';
@import './mixins.scss';
@import './colors.scss';

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

img {
    max-width: 100%;
    height: auto;
}

body {
    font-family: 'Helvetica', sans-serif;
    font-weight: 400;
    font-size: 18px;
}

button {}

.head {
    display: flex;
    align-items: flex-end;
    background-image: url('../img/head-bg.png');
    background-repeat: no-repeat;
    background-size: cover;
    overflow: hidden;
    padding: 20px 0;
    padding-top: 80px;
    &__container {
        @include container;
        margin: 0 auto !important;
        display: flex;
        justify-content: space-between;
        max-width: 1566px;
    }

    &__info {
        max-width: 594px;
        padding: 0 10px;
    }

    &__title {
        @include h1;
        margin: 17px 0 56px 0;
    }

    &__image {
        margin-bottom: -30px;
        display: flex;
        align-items: flex-end;
    }

    &__button {
        @include button;

        svg {
            margin-left: 16px;
        }
    }
}

.about-crm {

    &__container {
        @include container;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__info {
        max-width: 660px;
    }

    &__title {
        @include h2;
        margin-bottom: 57px;
    }

    &__subtitle {
        color: $black;
        line-height: 155%;
        margin-bottom: 16px;

    }

    &__subtitle, &__quote {
        max-width: 557px;
    }

    &__quote {
        color: $green;
        font-weight: bold;
        line-height: 155%;
        margin-bottom: 33px;
        p {
            color: $black;
            font-weight: normal;
        }
    }

    &__wrapper {

        border: 1px solid #C9C9C9;
        border-radius: 10px;
        padding: 22px 17px;
        &-title {
            @include h3;
            max-width: 458px;
            margin-bottom: 20px;
        }
        &-action {
            display: flex;
            align-items: center;
            margin-bottom: 13px;
            line-height: 155%;
            &:last-child {
                margin-bottom: 0;
            }
        }
        &-img {
            width: 39px;
            margin-right: 12px;
        }
    }

    &__image {}
}

@import './media.scss'