@media (max-width: 1200px) {
    .head {

        &__info {
            max-width: initial;
        }
    }
}

@media (max-width: 1024px) {
    .head {

        min-height: 100vh;

        &__container {
            flex-direction: column;
        }

        &__info {
            margin-bottom: 10vh;
        }

        &__title {
            margin: 17px 0 27px 0;
            max-width: 660px;
        }

        &__image {
            margin-bottom: -65px;
        }
    }

    .about-crm {

        &__container {
            flex-direction: column;
        }

        &__title {
            text-align: center;
            max-width: 600px;
            margin: 0 auto;
            margin-bottom: 39px;
        }

        &__info,
        &__subtitle {
            max-width: initial;
        }

        &__info {
            max-width: 90%;
        }

        &__image {
            margin-top: 91px;
        }
    }
}

@media (max-width: 768px) {}

@media (max-width: 640px) {
    body {
        font-size: 16px;
    }

    .about-crm {

        &__info {
            max-width: initial;
        }

        &__quote {
            p {
                display: inline;
            }
        }

        &__wrapper {

            &-action {
                align-items: flex-start;

                span {
                    max-width: 210px;
                    
                }
            }
        }
    }
}